*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  box-sizing: border-box;
  line-height: 1.6;
  font-family: "Solway", serif, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.Card {
  width: 100%;
  padding: 0 1.5rem 1.8rem 1.5rem;
  background-color: #95dada;
  border-radius: .6rem;
  box-shadow: 0 0.8rem 1.4rem rgba(0, 0, 0, 0.15);
  display: grid;
  grid-template-columns: -webkit-max-content;
  grid-template-columns: max-content;
  grid-template-rows: 1fr -webkit-max-content -webkit-min-content;
  grid-template-rows: 1fr max-content min-content;
  justify-items: center;
  grid-row-gap: 1.5rem;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  -webkit-transition: -webkit-transform 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transition: -webkit-transform 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transition: transform 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transition: transform 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86), -webkit-transform 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86); }
  .Card:hover {
    -webkit-transform: translateZ(0) scale(1.1);
            transform: translateZ(0) scale(1.1); }
  .Card__monster-name {
    color: #101010;
    font-size: 2.4rem;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden; }
  .Card__monster-email {
    font-size: 1.6rem;
    color: #101010;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden; }

.CardList {
  padding-top: 2rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(-webkit-max-content, 30rem));
  grid-template-columns: repeat(auto-fit, minmax(max-content, 30rem));
  justify-content: center;
  align-content: center;
  grid-column-gap: 3rem;
  grid-row-gap: 2.8rem;
  border-top: 1px solid #333; }

.SearchBox {
  display: block;
  font-family: inherit;
  font-size: 1.5rem;
  justify-self: center;
  width: 20rem;
  padding: 1.2rem 1.4rem 1.1rem 1.2rem;
  background-color: #a2dede;
  border: none;
  border-bottom: 3px solid transparent;
  -webkit-transition: border-bottom .2s ease-out;
  transition: border-bottom .2s ease-out; }
  .SearchBox:focus {
    outline: none;
    border-bottom: 3px solid #4186ad; }
  .SearchBox::-webkit-input-placeholder {
    color: #387394; }
  .SearchBox::-moz-placeholder {
    color: #387394; }
  .SearchBox:-ms-input-placeholder {
    color: #387394; }
  .SearchBox::-ms-input-placeholder {
    color: #387394; }
  .SearchBox::placeholder {
    color: #387394; }

.App {
  padding: 6rem 5vw;
  background-image: -webkit-gradient(linear, right top, left top, from(#071b52), to(teal));
  background-image: linear-gradient(to left, #071b52 0%, teal 100%);
  background-repeat: no-repeat;
  min-height: 100vh;
  display: grid;
  grid-template-columns: minmax(-webkit-max-content, 1fr);
  grid-template-columns: minmax(max-content, 1fr);
  grid-template-rows: 12rem -webkit-min-content 1fr;
  grid-template-rows: 12rem min-content 1fr;
  grid-row-gap: 1.8rem; }
  .App__heading {
    justify-self: center;
    font-family: "Bigelow Rules", cursive;
    font-size: 9.6rem;
    line-height: 1.2;
    color: #0ccac4; }

